import useUpbit from './hooks/useUpbit';
import useConnectUpbit from './hooks/useConnectUpbit';

export default function UpbitPriceContainer() {
  useConnectUpbit();

  const { isLoading, data } = useUpbit();

  if (isLoading) {
    return <p> </p>;
  }

  return (
    <p>
       BTC:
      {' '}
      {data.toLocaleString()}
    </p>
  );
}
